nav {
    z-index: 10;
}

.uk-navbar-nav>li>a, .uk-navbar-nav>li.uk-active>a {
    color: #FFF;
}

.uk-navbar-nav>li>a {
    font-size: 0.7rem;
}

@media (min-width: $s) {
    .uk-navbar-nav>li>a {
        font-size: 1rem;
    }
}

